import {
  Button,
  DialogTitle,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from '@mui/material';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import { useRouter } from 'next/router';
import React, { useEffect, useState } from 'react';
import { useAuthAPI } from '../../hooks/authAPI';
import EmbeddedRoleTable from './embeddedRoleTable';
import PolicyNameInput from './policyNameInput';

export default function AdminRolesTable({
  selectMode = false,
  embeddedMode = false,
  selectCallback = () => null,
  userNameProp = '',
  roleNameProp = '',
}) {
  const router = useRouter();
  const [open, setOpen] = useState(false);
  const [newRoleName, setNewRoleName] = useState('');
  const [roleInputError, setRoleInputError] = useState('');
  const [rolesList, setRolesList] = useState([]);
  const authAPI = useAuthAPI();

  const handleClickOpen = () => {
    setNewRoleName('');
    setRoleInputError('');
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
    setNewRoleName('');
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    if (newRoleName?.trim() && newRoleName.replaceAll('_', '').length > 2) {
      let data = await authAPI.addRole(newRoleName);
      if (data) {
        let newList = await authAPI.getRoles();
        setRolesList(newList);
      }
      handleClose();
    } else {
      setRoleInputError('Please enter a name. Must be 3 characters or longer.');
    }
  };

  const handleRowSelect = async (roleName = null) => {
    if (selectCallback && roleName) {
      await selectCallback(roleName);
    }
    handleClose();
  };

  const handleRouteToRolePage = (roleName = '') => {
    if (roleName) {
      router.push('/admin/role/' + roleName);
    }
  };

  const refreshContent = async () => {
    let data = [];
    if (userNameProp) {
      data = await authAPI.getUserRoles(userNameProp);
    } else if (roleNameProp) {
      data = await authAPI.getChildRoles(roleNameProp);
    } else {
      data = await authAPI.getRoles();
    }
    if (data) {
      setRolesList(data);
    }
  };

  useEffect(async () => {
    if (!embeddedMode) {
      await refreshContent();
    }
  }, []);

  useEffect(async () => {
    if (embeddedMode && !open) {
      refreshContent();
    }
  }, [open]);

  return (
    <>
      <strong style={{ float: 'left' }}>Roles</strong>
      {!selectMode ? (
        <Button
          style={{ float: 'right', marginBottom: '0.5em' }}
          onClick={handleClickOpen}
          data-testid='addRoleTestId'
        >
          Add Role
        </Button>
      ) : null}
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 300 }} aria-label='Admin Role Table'>
          <TableHead>
            <TableRow>
              <TableCell>Name</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rolesList?.map((currentRole, index) => {
              return (
                <TableRow key={index} hover>
                  {!selectMode ? (
                    <TableCell>
                      <Button
                        onClick={() => handleRouteToRolePage(currentRole)}
                      >
                        {currentRole}
                      </Button>
                    </TableCell>
                  ) : (
                    <TableCell>{currentRole}</TableCell>
                  )}
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
      <Dialog
        fullWidth={true}
        maxWidth={'sm'}
        open={open}
        onClose={handleClose}
        aria-labelledby='dialog-title'
        aria-describedby='dialog-description'
      >
        <DialogTitle id='dialog-title'>
          {embeddedMode ? 'Select New Role' : 'New Role'}
        </DialogTitle>
        <DialogContent>
          {embeddedMode ? (
            <EmbeddedRoleTable
              currentRole={roleNameProp}
              excludeList={rolesList}
              selectCallback={handleRowSelect}
            />
          ) : (
            <PolicyNameInput
              placeholder='New Role...'
              valueProp={newRoleName}
              setState={setNewRoleName}
              errorMessage={roleInputError}
            />
          )}
        </DialogContent>
        <DialogActions>
          {!embeddedMode ? (
            <Button onClick={handleSubmit}>Submit</Button>
          ) : null}
          <Button onClick={handleClose}>Close</Button>
        </DialogActions>
      </Dialog>
    </>
  );
}
