import {
  Box,
  CircularProgress,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from '@mui/material';
import React, { useEffect, useState } from 'react';
import { useAuthAPI } from '../../hooks/authAPI';

export default function EmbeddedRoleTable({
  currentRole = '',
  excludeList = [],
  selectCallback = () => null,
}) {
  const [rolesList, setRolesList] = useState([]);
  const [loading, setIsLoading] = useState(false);
  const authAPI = useAuthAPI();

  const handleRowSelect = async (roleName = null) => {
    if (selectCallback && roleName) {
      setIsLoading(true);
      await selectCallback(roleName);
    }
  };

  useEffect(async () => {
    setIsLoading(true);
    let data = await authAPI.getRoles();
    if (data) {
      excludeList.push(currentRole);
      const dataFilter = data.filter((role) => !excludeList.includes(role));
      setRolesList(dataFilter);
    }
    setIsLoading(false);
  }, []);

  return (
    <>
      {loading ? (
        <Box sx={{ display: 'flex', justifyContent: 'center' }}>
          <CircularProgress />
        </Box>
      ) : (
        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 300 }} aria-label='Admin Role Table'>
            <TableHead>
              <TableRow>
                <TableCell>Name</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {rolesList?.map((currentRole, index) => {
                return (
                  <TableRow
                    key={index}
                    onClick={() => handleRowSelect(currentRole)}
                    style={{ cursor: 'pointer' }}
                    hover
                  >
                    <TableCell>{currentRole}</TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
      )}
    </>
  );
}
